.body {
  background-color: black;
  margin-top: 0;
  display: flex;
  height: 100%;
}
.intro {
  color: white;
  margin-top: 10%;
  display: block;
  width: 50%;
}
.intro h1 {
  margin: 0 auto;
}
.intro p {
  margin-top: 0.55rem;
  font-size: 0.85rem;
}
.img {
  display: block;
  width: 50%;
}
.sec {
  text-align: center;
  color: white;
  background-color: black;
  font-size: 1.5rem;
  padding: 2px;
}
.Caraousal {
  align-items: center;
}

/* for shorter device display */

@media (max-width: 750px) {
  .intro h1 {
    font-size: 1.3rem;
    margin-bottom: 0;
  }
  .intro p {
    font-size: 0.6rem;
    margin: 6px auto;
  }
  .sec {
    font-size: 1rem;
    margin: 0.2rem auto;
    padding: 0.8px;
    width: 98%;
  }
}

@media (max-width: 350px) {
  .body {
    align-items: center;
    display: block;
  }
  .intro {
    color: white;
    margin: 20px auto 10px auto;
    display: block;
    width: 70%;
  }
  .intro h1,
  .intro p {
    margin: 2px;
  }
  .intro h1 {
    font-size: 1.1rem;
  }
  .intro p {
    font-size: 0.4rem;
  }
  .img {
    display: block;
    width: 50%;
    margin: 10px auto;
  }
  .sec {
    font-size: 0.7rem;
    padding: 1px;
    width: 95%;
  }
  .sec h2 {
    margin: 0.4rem auto;
  }
  .Caraousal div {
    height: 60%;
  }
}
