.Summary {
  color: rgb(85, 85, 85);
  display: flex;
  margin: 15px auto;
  text-align: center;
  padding: 0.8rem;
  width: 80%;
}

.Summary p {
  display: inline-block;
  font-size: 1.5rem;
  font-weight: bold;
  width: 90%;
  margin: auto;
}

@media (max-width: 750px) {
  .Summary {
    padding: 0.5rem;
    width: 90%;
  }
  .Summary p {
    margin: auto;
    padding: 0;
    width: 100%;
    font-size: 1.2rem;
  }
}

@media (max-width: 350px) {
  .Summary {
    padding: 0.7rem;
    width: 92%;
    margin: auto;
  }
  .Summary p {
    font-size: 0.8rem;
    margin: 0.4rem auto;
    padding: 0;
  }
}
